.reviews {
  padding: 50px 0;
  background-color: $color-dark-tangerine;

  &__element {
    &.is-active {
      & .reviews__content {}

      & .reviews__more {
        display: none;
        
        & svg {
          transform: rotate(180deg);
        }
      }

      & .reviews__more--less {
        display: block;
      }
    }
  }

  &__list {
    max-width: 660px;
    list-style: none;
    margin: 0;
    margin-bottom: 30px;
    margin-left: auto;
    margin-right: auto;
    padding: 0;

    @include vp-767 {
      margin-bottom: 2px;
    }
  }

  &__item {
    margin-bottom: 20px;
    padding-left: 100px;
    
    @include vp-767 {
      padding: 0;
    }
  }

  &__name {
    font-weight: 500;
    font-size: 16px;
    line-height: normal;
    color: $color-nero;
    margin: 0;
    margin-bottom: 7px;
  }

  &__raiting {
    display: flex;
    margin-bottom: 5px;
  }
  
  &__stars {
    display: flex;
    margin: 0;
    margin-right: 10px;
    column-gap: 4px;
  }

  &__star {
    display: flex;
    width: 20px;
    height: 20px;

    & svg {
      margin: auto;
    }
  }

  &__raiting-value {
    margin: 0;
    color: $color-nero;
  }

  &__text {
    width: 98%;
    font-weight: 500;
    font-size: 14px;
    line-height: normal;
    color: $color-nero;
    margin: 0;
  }

  &__more {
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;
    font-size: 15px;
    line-height: 15px;
    color: $color-dark-tangerine;
    background-color: $color-nero;
    border: 1px solid transparent;
    border-radius: 5px;
    margin: 0;
    margin-left: auto;
    margin-right: auto;
    padding: 9px 18px;
    cursor: pointer;
    transition: opacity $trans-default;

    &:hover,
    &:focus {
      opacity: 0.7;
    }

    &:active {
      opacity: 0.5;
    }

    & svg {
      width: 16px;
      height: 16px;
      margin-left: 20px;
      transform: translateY(2px);
      transition: $trans-default;
    }

    &--less {
      display: none;

      & svg {
        transform: rotate(180deg);
      }
    }
  }
}
