.title-h1 {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  font-size: 40px;
  font-weight: 500;
  line-height: normal;
  color: $color-silver;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 30px;
  text-align: center;

  & span {
    margin-right: 5px;
    color: $color-splash;

    @include vp-1023 {
      width: 100%;
    }
  }

  @include vp-1023 {
    margin-bottom: 15px;
  }
}