.goods {
  position: relative;
  z-index: 1;
  background-color: $color-dark-tangerine;
  padding: 50px 0;
  
  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 45px;
  }

  &__item {}

  &__item-wrap {}

  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    transition: opacity $trans-default;
    
    & .goods__picture {
      transition: transform $trans-default;
    }
    
    &:hover,
    &:focus {
      opacity: 70%;

      & .goods__picture {
        transform: scale(1.05);
      }
    }

    &:active {
      opacity: 50%;
    }
  }

  &__picture {
    border-radius: 5px;
    border: 2px solid $color-nero;
    margin-bottom: 20px;
    padding: 7px;

    & img {
      border-radius: 5px;
      width: 195px;
      height: 233px;
      object-fit: cover;
    }
  }

  &__price {
    font-weight: 500;
    font-size: 22px;
    line-height: 0.95;
    text-align: center;
    filter: drop-shadow(2px 5px 2px rgba($color-nero, 30%));
    margin: 0;
  }

  &__mask {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 90%;
    transform: translate(-50%, -50%);
    z-index: -1;
  
    & svg {
      width: 100%;
      height: 100%;
    }
  }

  &__content-tab {
    display: flex;
    justify-content: center;
  }
  
  &__element-tab {
    max-width: 850px;
  }

  &__card-tab-element {
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
    justify-content: center;
  }

  &__content-box {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    background-color: rgba($color-dark-tangerine, 90%);
    box-shadow: 0 4px 4px 0 rgba($color-night-rider, 30%);
    padding: 30px;
    border-radius: 12px;

     &--cover {
      flex-direction: column;

      & .goods__content-column--img {
        margin-top: auto;
      }

      & .goods__content-imgs {
        display: flex;
        gap: 30px;
      }

      & .goods__content-picture {
        display: flex;
        flex-wrap: wrap;
        gap: 20px;
      }
    }
  }
  
  &__content-picture {
    &-link {
      margin-bottom: 30px;

      @include vp-767 {
        margin-bottom: 0;
      }
    }

    & img {
      border-radius: 5px;
      width: 108px;
      height: 129px;
      object-fit: cover;
      box-shadow: 0 4px 4px 0 $color-night-rider;
    }

    @include vp-767 {
      display: flex;
      gap: 10px;
    }
  }

  &__content-column {
    display: flex;
    gap: 30px;
    margin-bottom: 30px;
    

    &--hard {
      margin-right: 45px;

      &:last-child {
        margin-right: 0;
      }
    }

    @include vp-767 {
      flex-direction: column;
      gap: 20px;
    }
  }

  &__content-title {
    text-align: center;
    margin: 0;
    margin-bottom: 20px;
    filter: drop-shadow(2px 5px 2px rgba($color-nero, 30%));
  }

  &__content-desc {
    & p {
      font-weight: 700;
      font-size: 14px;
      line-height: normal;
      color: $color-nero;
      max-width: 500px;
    }

    &--align {
      text-align: right;

      @include vp-767 {
        text-align: left;
      }
    }

    &--order {
      order: -1;
    }

    &--hard {}

    @include vp-767 {
      order: -1;
    }
  }
}
