.field-contain {
  width: 100%;

  &__wrap {
    position: relative;
  }

  &__input {
    font-size: 24px;
    line-height: normal;
    width: 100%;
    background-color: $color-silver;
    color: $color-nero;
    padding: 23px 20px;
    border-radius: 30px;
    border: 2px solid $color-silver;
    transition: border-color $trans-default, transform $trans-default;

    &:hover {
      outline: none;
      border-color: $color-tangaroa;
    }

    &:focus {
      border-color: $color-navy-blue;
    }

    &:focus + .field-contain__placeholder-text .field-contain__text {
      color: $color-navy-blue;
      background-color: $color-silver; // -----
      border-radius: 12px;
      transform: translate(0, -100%);
      transition: border-color $trans-default, color $trans-default, transform $trans-default;
    }
  }

  &__placeholder-text {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0.5rem;
    right: 0;
    border: 3px solid transparent;
    background-color: transparent;
    display: flex;
    align-items: center;
    pointer-events: none;
    color: $color-matisse;
  }

  &__text {
    transition: transform $trans-default, font-size $trans-default, background-color $trans-default, color $trans-default;
    font-size: 24px;
    line-height: normal;
    padding: 0 0.5rem;

    &--focus {
      background-color: $color-silver; // -----
      border-radius: 12px;
      transform: translate(0, -100%);
    }
  }
}
