.callback {
  &__row {
    // margin-bottom: 25px;
  }

  &__action {
    margin-top: 20px;
  }

  &__btn {
    max-width: 350px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;
    font-size: 24px;
    line-height: normal;
    color: $color-nero;
    background-color: $color-dark-tangerine;
    border: 1px solid transparent;
    border-radius: 5px;
    margin: 0;
    margin-left: auto;
    margin-right: auto;
    padding: 16px 18px;
    cursor: pointer;
    transition: opacity $trans-default;

    &:hover,
    &:focus {
      opacity: 0.7;
    }

    &:active {
      opacity: 0.5;
    }
  }
}
